body {
    background-color: #222831;
    color: #eeeeee;
}

h1,
h2 {
    color: #d65a31;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.preview {
    background-color: #393e46;
    min-height: 150px;
    width: 60vw;
    margin-top: 20px;
    padding: 0px 25px 0px 25px;
    border-radius: 15px;
}

.rezept,
.rezept-edit {
    width: 70%;
    background-color: #393e46;
    padding: 0px 30px 30px 30px;
    border-radius: 15px;
}

.zutaten {
    margin-top: 50px;
}

.interactions {
    display: flex;
    color: #d65a31;
    font-style: italic;
}

.interactions > p {
    cursor: pointer;
    padding: 15px;
}

.zutat-bearbeiten {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.zutaten-beearbeiten {
    margin-top: 50px;
}

.zutat {
    margin-right: 20px;
}

.zutat-input {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-size: 16px;
}

.text-bearbeiten {
    display: flex;
    flex-direction: column;
}

input,
textarea {
    background-color: #393e46;
    border-radius: 5px;
    border-color: #eeeeee;
    color: #eeeeee;
    border: 2px solid #eeeeee;
    resize: vertical;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.titel-input {
    margin-top: 30px;
    font-size: 32px;
    font-weight: bold;
    color: #d65a31;
}

.beschreibung-input {
    margin-top: 30px;
    font-size: 16px;
}

.entfernen {
    height: 20px;
}

.add {
    margin-top: 50px;
}

button {
    background-color: #d65a31;
    color: #eeeeee;
    border: 0px;
    border-radius: 5px;
}
